.ListItems{
    display: flex;
    flex-direction: row;
    width: 360px;
    background-color: white;
    height: 84px;
    margin-top: 5px;
    /* border-radius: 10px; */
    border-bottom: 1px solid #E6E6E6;
    /* border: 1px solid #E6E6E6; */
    /* box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25); */
    z-index: -1;
}

.ProfileListImage{
    height: 60px;
    width: 60px;
    border-radius: 30px;
    margin-right: 10px;
    margin-top: 10px;
    margin-left: 22px;
}

.NameAndText{
    margin-left: 10px;
    display: flex;
    flex-direction: column;
}

.nameTag{
    font-weight: 500;
    font-size: 18px;
    letter-spacing: -0.02em;
    color: #3C3C3C;
}

.messageTag{
    font-weight: normal;
    margin-left: -22px;
    margin-top: -16px;
    font-size: 14px;
    color: #ABABAB;
}