.ChatBox{
    border: 2px solid rgba(214, 214, 214, 0.431);
    border-radius: 22px;
    background: #FFFFFF;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    position: fixed;
    width: 780px;
    height: 608px;
    left: 610px;
    top: 128px;
}

.Nav{
    height: 80px;
    background: #303C6C;
    border-radius: 20px 20px 0px 0px;
    display: flex;
    flex-direction: row;
}

.Nav > img{
    width: 60px;
    height: 60px;
    border-radius: 30px;
    position: relative;
    margin: 10px;
    margin-left: 20px;
}

.NavName{
    letter-spacing: -0.02em;
    color: #FFFFFF;
    font-size: 20px;
    margin-top: 28px;
    margin-left: 12px;
}

.ChatDisplay{
    margin-top: -22px;
    width: 100%;
    height: 440px;
    background-color: white;
}

.LastTab{
    display: flex;
    flex-direction: row;
}

.MessageTextInputBox{
  margin-right: 20px;
  margin: 20px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: -0.02em;
  color: #3C3C3C;
  /* margin-top: 40px; */
  /* margin-left: -560px; */
  width: 100%;
  height: 40px;
  padding-left: 20px;
  border: 2px solid rgba(214, 214, 214, 0.945);
  border-radius: 10px;
  background: #FFFFFF;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.iconToSend{
    position: relative;
    
    right: 20px;
    top: 20px;
    margin-bottom: 70px;
    font-size: 20px;
    padding: 10px;
    border-radius: 40px;
    background-color: #C4C4C4;
}