.List{
    width: 360px;
    height: 540px;
    max-height: 540px;
    z-index: 30;
    border: 2px solid rgba(214, 214, 214, 0.431);
    border-radius: 10px;
    background: #FFFFFF;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    margin-top: 20px;
    margin-left: -180px;
    overflow: scroll;
}

.-webkit-scrollbar {
    display: none;
    width: 5px;
    height: 8px;
    background-color: #aaa;
}


